
body, html {
  margin: 0;
  padding: 0;
}
body, html, #app {
  height: 100%;
}
#app {
  background-color: #f8f8f8;
}
.wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
h1 {
  color: #6ea5d4;
}
h2 {
  font-size: 18px;
}
input {
  outline: none;
  border: none;
  border-bottom: 2px solid #6ea5d4;
  padding: 8px 16px 3px;
  font-size: 20px;
}
ul {
  list-style: none;
}
li {
  margin-bottom: 20px;
}
label {
  margin-right: 20px;
  vertical-align: -webkit-baseline-middle;
}



